/* eslint-disable */
import Main from '@/pages/TheMain'
import MenuPage from '@/pages/MenuPage'
import {
  createRouter,
  createWebHistory
} from 'vue-router'
const routes = [{
    path: '/',
    component: Main
  },
  {
    path: '/menu',
    component: MenuPage
  }
]

const router = createRouter({
  routes,
  history: createWebHistory(),
  mode: 'hash'
})

export default router
