<template>
  <section class="food-menu">
    <div class="container">
      <div class="food-menu__inner">
        <h2 class="food-menu__title">Бургеры</h2>
        <input
          class="food-menu__search"
          type="search"
          placeholder="Поиск вкусняшек...."
          v-model="searchQuery"
        />
        <select class="food-menu__select">
          <option value="" disabled>Сортировать</option>
          <option value="name">По названию</option>
          <option value="price">По цене</option>
        </select>
      </div>
      <ul class="food-menu__list" v-if="foodList.length > 0">
        <li
          class="food-menu__item"
          v-for="item of foodListFilter"
          :key="item.id"
        >
          <img
            class="food-menu__img"
            :src="`${item.img}`"
            alt="burger"
            width="380"
            height="430"
          />
          <h3 class="food-menu__heading">
            {{ item.name }}
          </h3>
          <span class="food-menu__price"> ${{ item.price }} </span>
          <button class="food-menu__btn" type="button">купить</button>
        </li>
      </ul>
      <img
        src="https://media.tenor.com/wpSo-8CrXqUAAAAi/loading-loading-forever.gif"
        alt=""
        v-else
      />
      <ul class="page">
        <li
          class="page__numbers"
          v-for="currentPage in totalPages"
          :key="currentPage"
          :class="{
            active: page === currentPage,
          }"
          @click="changePage(currentPage)"
        >
          {{ currentPage }}
        </li>
      </ul>
      <h2 class="food-menu__title">Дополнительно</h2>
      <ul class="food-menu__list" v-if="foodListAdditional.length > 0">
        <li
          class="food-menu__item"
          v-for="item of foodListAdditional"
          :key="item.id"
        >
          <img
            class="food-menu__img"
            :src="`${item.img}`"
            alt="burger"
            width="380"
            height="430"
          />
          <h3 class="food-menu__heading">
            {{ item.name }}
          </h3>
          <span class="food-menu__price"> ${{ item.price }} </span>
          <button class="food-menu__btn" type="button">купить</button>
        </li>
      </ul>
      <img
        src="https://media.tenor.com/wpSo-8CrXqUAAAAi/loading-loading-forever.gif"
        alt=""
        v-else
      />
    </div>
  </section>
</template>
<script lang="js">
import axios from 'axios'
export default {
  data: () => ({
    foodList: [],
    foodListAdditional: [],
    searchQuery: '',
    page: 1,
    limit: 12,
    totalPages: 0
  }),

  methods: {
    async foodListCreate () {
      try {
        const response = await axios.get('https://free-food-menus-api-production.up.railway.app/burgers', {
          params: {
            _page: this.page,
            _limit: this.limit
          }
        })
        this.totalPages = Math.ceil(response.headers['x-total-count'] / this.limit)
        this.foodList = response.data
      } catch (error) {
        console.log(error)
      }
    },
    changePage (pageNum) {
      this.page = pageNum
      this.foodListCreate()
    },
    foodListCreateAdditional () {
      try {
        setTimeout(async () => {
          const response = await axios.get('https://free-food-menus-api-production.up.railway.app/our-foods?_limit=3')
          this.foodListAdditional = response.data
        }, 1000)
      } catch (error) {
        console.log(error)
      }
    }
  },

  computed: {
    foodListFilter () {
      return this.foodList.filter(item => item.name.toLowerCase().includes(this.searchQuery.toLowerCase()))
    }
  },

  mounted () {
    this.foodListCreate()
    this.foodListCreateAdditional()
  }
}
</script>
<style lang="scss" scoped>
$primary: #23adad;
$greyLight: #23adade1;
$greyLight-2: #cbe0dd;
$greyDark: #2d4848;
.food-menu {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 35px;
    gap: 30px;
  }

  &__title {
    font-size: 44px;
    text-transform: uppercase;
    color: #1b2a49;
  }

  &__search {
    border: 1px solid #dedede;
    border-radius: 22px;
    padding: 10px 30px;
    font-size: 16px;
    color: #443737;
    flex-grow: 1;
  }

  &__select {
    border: 1px solid #dedede;
    border-radius: 22px;
    max-width: 279px;
    width: 100%;
    padding: 10px 30px;
    font-size: 16px;
    color: #443737;
    cursor: pointer;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(140px, 380px));
    gap: 15px;
    margin-bottom: 50px;
  }

  &__item {
    text-align: center;
  }

  &__img {
    margin-bottom: 20px;
  }

  &__heading {
    font-weight: 600;
    font-size: 30px;
    color: #1b2a49;
    margin-bottom: 5px;
  }

  &__price {
    font-weight: 700;
    font-size: 20px;
    color: #979797;
    display: block;
    margin-bottom: 20px;
  }

  &__btn {
    background-color: #fbb731;
    border-radius: 5px;
    text-transform: uppercase;
    padding: 10px 30px;
  }
}

.items-list {
  max-width: 90vw;
  margin: 2rem;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 3rem;
  justify-content: center;
  align-content: center;

  @media only screen and (max-width: 600px) {
    grid-template-columns: repeat(2, 1fr);
  }
}

.item {
  width: 10rem;
  height: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--greyDark);
  cursor: pointer;

  span {
    background: #ffffff;
    box-shadow: 0 0.8rem 2rem rgba(#5a6181, 0.05);
    border-radius: 0.6rem;
    padding: 2rem;
    font-size: 3rem;
    transition: all 0.3s ease;
  }

  &:hover {
    span {
      transform: scale(1.2);
      color: $primary;
    }
  }

  p {
    font-size: 1.2rem;
    margin-top: 1rem;
    color: $greyLight;
  }
}

.page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  margin: 3rem;
  border-radius: 0.6rem;
  background: #ccc;
  box-shadow: 0 0.8rem 2rem rgba(#5a6181, 0.05);

  &__numbers,
  &__btn,
  &__dots {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0.8rem;
    font-size: 1.4rem;
    cursor: pointer;
    color: #000;
  }

  &__dots {
    width: 2.6rem;
    height: 2.6rem;
    color: $greyLight;
    cursor: initial;
  }

  &__numbers {
    width: 2.6rem;
    height: 2.6rem;
    border-radius: 0.4rem;

    &:hover {
      color: $primary;
    }

    &.active {
      color: #000;
      background: $primary;
      font-weight: 600;
      border: 1px solid $primary;
    }
  }

  &__btn {
    color: $greyLight;
    pointer-events: none;

    &.active {
      color: $greyDark;
      pointer-events: initial;

      &:hover {
        color: $primary;
      }
    }
  }
}
</style>
